.header {
  background: url("../img/img-app.png") bottom right/652px 680px no-repeat, var(--bg-header);
  min-height: 800px;
  position: relative;
}

.header__nav {
  margin-top: 28px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  align-content: center;
}

.header__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  gap: 30px;
}

.header__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  transition: 0.3s;
  color: var(--color-white);
}
.header__link:hover {
  color: var(--color-header-hover);
}

.header__inner {
  margin-top: 228px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.header__title {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 80px;
  line-height: 80px;
  color: var(--color-white);
}

.header__text {
  font-weight: 400;
  font-size: 30px;
  line-height: 44px;
  color: var(--color-white);
}

.header__button {
  border: none;
  background: var(--easy-pink);
  border-radius: 3px;
  padding: 20px 40px;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  transition: 0.5s;
  color: var(--color-white);
}
.header__button:hover {
  background: var(--color-button-hover);
  box-shadow: 5px 5px 10px black;
}

.header__arrow {
  background: none;
  border: none;
  position: absolute;
  bottom: 30px;
  left: 50%;
  right: 50%;
  transform: translateY(-50%, -50%);
  cursor: pointer;
}

.application {
  background: url("../img/application.png") bottom center/1200px 656px no-repeat, var(--application-bg);
  min-height: 957px;
  padding-top: 100px;
}

.application__title {
  margin: 0 auto;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: var(--text-easy-gray);
  text-align: center;
}

.application__text {
  font-weight: 400;
  font-size: 38px;
  line-height: 48px;
  text-align: center;
  color: var(--color-white);
}

.advantages {
  padding: 80px 72px 160px 72px;
  background-color: var(--advantages-bg);
}

.advantages__wrapper {
  margin: 0 auto;
  max-width: 1440px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.advantages__holder {
  display: flex;
  align-items: center;
  gap: 109px;
  margin-bottom: 160px;
}

.advantages__inner--expand {
  order: 2;
}

.advantages__inner {
  max-width: 310px;
}

.advantages__title {
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: var(--text-easy-gray);
}

.advantages__subtitle {
  font-weight: 400;
  font-size: 38px;
  line-height: 48px;
  color: var(--advantages-subtitle);
}

.advantages__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  color: var(--advantages-text);
}

.interesting {
  background-color: var(--interesting-bg);
}

.interesting__wrapper {
  margin: 0 auto;
  max-width: 1440px;
  padding: 0 72px;
}

.interesting__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: space-around;
}

.interesting__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-content: center;
}

.interesting__subtitle {
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  color: var(--interesting-subtitle);
  margin-top: 20px;
  margin-bottom: 0;
}

.interesting__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  text-align: center;
  color: var(--interesting-text);
  max-width: 371px;
}

.statistics {
  background-color: var(--statistics-bg);
  padding: 160px 0;
}

.statistics__wrapper {
  margin: 0 auto;
  max-width: 1440px;
  padding: 0 72px;
}

.statistics__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.statistics__item {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
}

.statistics__numder {
  font-weight: 400;
  font-size: 59px;
  line-height: 90px;
  color: var(--statistics-numder);
}

.statistics__text {
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  color: var(--statistics-text);
}

.statistics__text--outline {
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
}

.promo {
  padding: 80px 72px 160px 72px;
  background-color: var(--advantages-bg);
}

.promo__wrapper {
  margin: 0 auto;
  max-width: 1440px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.promo__holder {
  display: flex;
  align-items: center;
  gap: 109px;
  margin-bottom: 160px;
}

.promo__inner--expand {
  order: 2;
}

.promo__inner {
  max-width: 310px;
}

.promo__title {
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: var(--text-easy-gray);
}

.promo__subtitle {
  font-weight: 400;
  font-size: 38px;
  line-height: 48px;
  color: var(--advantages-subtitle);
}

.promo__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  color: var(--advantages-text);
}

.promo__link {
  color: var(--promo-link);
  font-weight: 700;
  border-bottom: 1px solid var(--promo-link);
}

.promo__img {
  width: 519px;
  height: 414px;
  background: var(--promo-img-bg);
}

.reviews {
  background-color: var(--reviews-bg);
  padding: 50px 72px;
}

.reviews__wrapper {
  margin: 0 auto;
  max-width: 1440px;
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: center;
}

.reviews__inner {
  background: url("../img/reviews-img.png") center top/58px 48px no-repeat;
  max-width: 700px;
  min-height: 374px;
  padding-bottom: 76px;
}

.reviews__text {
  padding-top: 120px;
  font-weight: 400;
  font-size: 24px;
  line-height: 40px;
  text-align: center;
  color: var(--reviews-text);
}

.reviews__row {
  position: relative;
  margin: 0 auto;
  display: flex;
  width: 302px;
  align-items: center;
  justify-content: space-between;
}

.reviews__row::after {
  position: absolute;
  content: "";
  display: block;
  background: var(--reviews-after);
  width: 30px;
  height: 1px;
  left: 42%;
  right: 50%;
}

.reviews__excerpt {
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  color: var(--reviews-text);
}

.reviews__excerpt--fat {
  font-weight: 700;
  font-size: 16px;
  line-height: 30px;
}

.reviews__points {
  display: flex;
}

.reviews__point {
  background: none;
  border: none;
}

.reviews__point .reviews__style {
  display: block;
  width: 7px;
  height: 7px;
  background: rgba(34, 34, 34, 0.4);
  cursor: pointer;
}

.reviews__point .reviews__style--active {
  background: #222222;
}

.marketing {
  display: flex;
  flex-direction: column;
  background: url("../img/marketing-app.png") bottom center/100% 518px no-repeat, var(--marketing-bg);
  height: 671px;
}

.marketing__inner {
  margin: 0 auto;
  margin-bottom: 115px;
}

.footer {
  background-color: var(--footer-bg);
  min-height: 705px;
  padding-top: 129px;
  padding-bottom: 60px;
}

.footer__wrapper {
  width: 575px;
  height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.footer__title {
  font-weight: 400;
  font-size: 38px;
  line-height: 48px;
  text-align: center;
  color: var(--footer-title);
}

.footer__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  text-align: center;
  color: var(--footer-text);
}

.footer__form {
  display: flex;
  justify-content: center;
}

.footer__input {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  padding: 22px 155px 22px 10px;
  outline: none;
}

.footer__button {
  border: none;
  background: var(--easy-pink);
  border-radius: 0px 3px 3px 0px;
  padding: 22px 40px;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  transition: 0.5s;
}
.footer__button:hover {
  background: var(--color-button-hover);
  box-shadow: 5px 5px 10px black;
  color: var(--color-white);
}

.footer__logo {
  padding-top: 146px;
}

.footer__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  width: 575px;
  flex-direction: row;
  justify-content: space-between;
}

.footer__link {
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
  text-align: center;
  color: var(--footer-text);
}

.get-start {
  background: url("../img/get-bg.png") center center/cover no-repeat;
  display: flex;
  height: 474px;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
}

.get-start__title {
  font-weight: 400;
  font-size: 38px;
  line-height: 48px;
  text-align: center;
  color: var(--get-start-text);
}

.get-start__inner {
  margin-top: 40px;
  display: flex;
  width: 395px;
  column-gap: 10px;
  justify-content: center;
}

.get-start__button {
  border: none;
  background: var(--easy-pink);
  border-radius: 3px;
  padding: 20px 40px;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  transition: 0.5s;
  color: var(--get-start-text);
  cursor: pointer;
}

.get-start__button--active {
  background: none;
  border: 1px solid var(--border-button);
  border-radius: 0px;
}